/* Lightbox styles */
amp-img[aria-label=lightbox] {
  cursor: zoom-in;
}

amp-image-lightbox img {
  object-fit: scale-down;
}
amp-image-lightbox button:last-of-type {
  cursor: zoom-out;
  min-width: 100%;
  min-height: 100%;
}

amp-image-viewer img {
  max-width: fit-content;
  transition: width 0.25s linear;
}

amp-lightbox {
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1050;
}

/* Heading styles */
h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.7rem;
}

h4 {
  font-size: 1.48rem;
}

h5 {
  font-size: 1.3rem;
}

h6 {
  font-size: 1.25rem;
}

/* General styles */
a.vglnk {
  border-bottom: 2px dashed gray;
  box-shadow: unset;
}

figure.shadow-none *, .shadow-none {
  box-shadow: none;
}

kbd {
  background-color: #eee;
  border-radius: 3px;
  border: 1px solid #b4b4b4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 2px 0 0 rgba(255, 255, 255, 0.7) inset;
  display: inline-block;
  font-size: 0.85em;
  line-height: 1;
  padding: 2px 4px;
  white-space: nowrap;
}

.mono {
  font-family: Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
}

.container {
  width: 96%;
  max-width: 100%;
}

.inline-fix + * {
  display: inline;
}
.inline-fix + * + .badge {
  vertical-align: text-top;
}

/* Component styles */
.article-post del {
  color: #999;
  text-decoration-color: #f05f70;
}
.article-post del a {
  box-shadow: none;
  border-bottom: 1px solid #ddd;
  text-decoration: inherit;
}
.article-post .box-highlight {
  box-shadow: 1px 2px 15px #675bff;
}
.article-post figcaption {
  font-size: smaller;
}
.article-post a > code {
  padding: 0.2rem 0.4rem;
  color: #bd4147;
  background-color: #f7fafb;
  box-shadow: inset 0 -2px 0 #ffe51f, 0 2px 0 #ffe51f;
}
.article-post a.citation, .article-post a.footnote, .article-post a.reversefootnote {
  box-shadow: none;
  background-color: #ffe51f;
}

.card .row, .card > a {
  height: 100%;
}

.listfeaturedtag .card .thumbnail img {
  width: 500px;
  height: 258px;
}
.listfeaturedtag .card-list {
  width: 100%;
}
.listfeaturedtag .card-list .thumbnail img {
  width: 500px;
  height: 150px;
  object-fit: cover;
}
.listfeaturedtag .card-list .card-title {
  font-size: 1rem;
  margin-top: -0.5rem;
}
.listfeaturedtag .card-list .wrapthumbnail {
  height: 150px;
}

.thumbnail.fit-left img {
  object-position: left;
}
.thumbnail.fit-right img {
  object-position: right;
}

